:root{
    --header-height: 60px;
}

header{
    height: var(--header-height);
    background-color: white;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.228);
}

nav{
    height: var(--header-height);
    display: flex;
    justify-content: center;
    gap: 100px;
}

nav > .logo-img{
    margin: 1px 1px 1px 1px;
}

nav .create-button{
    display: flex;
    justify-content: space-around;
}

nav .create-button > button {
    width: 150px;
    background-color: white;
    border: 1px ridge rgba(0, 0, 0, 0.1);
    transition: .35s;
}

nav .create-button > button:hover {
    background-color: rgba(212, 212, 212, 0.151);
}

nav .create-button > button img{
    width: 30px;
    height: 30px;
    margin-right: 10px;
    margin: auto;
}

nav .create-button > button div{
    display: flex;
    justify-content: center;
}

nav .create-button > button p{
    font-size: 1.1em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
    width: 70px;
    margin: 0;
}

main div > h1 {
    text-align: center;
}

nav .cart-button button{
    width: 150px;
    background-color: rgb(255, 0, 0);
    border: 1px ridge rgba(0, 0, 0, 0.1);
    transition: .35s;
    height: var(--header-height);
    color: white;
}
nav .cart-button button:hover{
    background-color: rgb(194, 31, 31);
}